import React, { Component, Fragment } from 'react';

import PropTypes from 'prop-types';
import { inject, observer} from 'mobx-react';
import AppStore from '../../store/AppStore6';
import loadable from '@loadable/component';
import { LazyRenderer, EmptyComponent } from './../../utility/lazyRenderer';

import Page6Card1 from './subComponent/page6/page6card1';
import Page6Card2 from './subComponent/page6/page6card2';



@inject('appStore')
@observer
class LandingPage6 extends Component {
  constructor(props) {
    super(props);
}

static propTypes = {
    appStore: PropTypes.object.isRequired,
};

static defaultProps = {
    appStore: new AppStore(),
};

  componentDidMount(){
    if(typeof(landingPageDidMount)!=="undefined"){
      landingPageDidMount=true;
    }
  }
  
  render() {
    const { data } = this.props.appStore;
    return (
      <div>
        {!data.cards[0].loadError && <Page6Card1 data={data.cards[0]} lang={data.lang} appStore={this.props.appStore}></Page6Card1>}
{!data.cards[1].loadError && <Page6Card2 data={data.cards[1]} lang={data.lang} appStore={this.props.appStore}></Page6Card2>}

      </div>
    )
  }
}

export default LandingPage6;
